import React from "react";
import "./App.css";
import Table from "./Table";
import logo from "./HIguchi.jpg";
import animal from "./animals.jpg";
import fox from "./fox.jpg";
import dolphin from "./dolphin.jpg";
import koala from "./koala.jpg";
import pug from "./pug.jpg";
import rabbit from "./rabbit.jpg";
import mammal from "./mammal.jpg";

const characters = [
  {
    image: logo,
    name: "ゴン",
    job: "強化系",
    age: 12
  },
  {
    image: animal,
    name: "キルア",
    job: "変化形",
    age: 12
  },
  {
    image: fox,
    name: "クラピカ",
    job: "特殊系",
    age: 18
  },
  {
    image: dolphin,
    ame: "レオリオ",
    job: "放出系",
    age: 20
  },

  {
    image: koala,
    name: "ネテロ",
    job: "ハンター協会会長",
    age: "超じじい"
  },
  {
    image: pug,
    name: "ゼノ",
    job: "一日一殺",
    age: "じじい"
  }
];

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { characters };
  }

  render() {
    return (
      <div className="App">
        <div className="higu">{<img src={logo} />}</div>
        <Table
          characterData={this.state.characters}
          removeCharacter={index => {
            this.removeCharacter(index);
          }}
        />
        Hello aaaaaaaa
      </div>
    );
  }

  removeCharacter(index) {
    const characters = this.state.characters.filter((_, i) => i !== index);
    this.setState({ characters });
  }
}

export default App;
